'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';

export function useRiwayatTransaksiPage() {
    // State Data
    const [transaksis, setTransaksis] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    
    // State Filter
    const [searchQuery, setSearchQuery] = useState('');
    
    // State Modal
    const [selectedStruk, setSelectedStruk] = useState<any | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    
    // State Aksi
    const [isProcessing, setIsProcessing] = useState(false);

    // 1. Fetch Data
    const fetchHistory = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/transaksiRestos');
            const data = response.data.data ? response.data.data : response.data;
            
            // Sort dari yang terbaru
            const sortedData = Array.isArray(data) 
                ? data.sort((a: any, b: any) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime()) 
                : [];

            setTransaksis(sortedData);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat riwayat transaksi.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { 
        fetchHistory(); 
    }, []);

    // 2. Filter Logic
    const filteredData = transaksis.filter(item => 
        item.kode_transaksi?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (item.nama_pelanggan && item.nama_pelanggan.toLowerCase().includes(searchQuery.toLowerCase()))
    );

    // 3. Handlers
    const handleViewDetail = (transaksi: any) => {
        setSelectedStruk(transaksi);
        setIsDialogOpen(true);
    };

    const handleUpdateStatus = async (id: number, newStatus: string) => {
        setIsProcessing(true);
        try {
            await axios.patch(`/api/transaksiRestos/${id}/status`, { status: newStatus });
            
            toast.success(`Status berhasil diubah menjadi ${newStatus}`);
            
            setIsDialogOpen(false);
            fetchHistory(); // Refresh data
            
        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Gagal mengubah status.");
        } finally {
            setIsProcessing(false);
        }
    };

    return {
        // Data
        transaksis: filteredData, // Return data yang sudah difilter
        loading,
        
        // Filter State
        searchQuery,
        setSearchQuery,
        
        // Modal State
        selectedStruk,
        isDialogOpen,
        setIsDialogOpen,
        isProcessing,

        // Actions
        fetchHistory,
        handleViewDetail,
        handleUpdateStatus
    };
}