'use client';

import { RefreshCcw } from 'lucide-react';
import { Button } from '@/components/ui/button';

// 1. Import Custom Hook
import { useRiwayatTransaksiPage } from '@/hooks/karyawan/useRiwayatTransaksiPage';

// 2. Import Komponen UI
import RiwayatFilter from '@/components/karyawan/riwayat-transaksi/RiwayatFilter';
import RiwayatTable from '@/components/karyawan/riwayat-transaksi/RiwayatTable';
import RiwayatStrukDialog from '@/components/karyawan/riwayat-transaksi/RiwayatStrukDialog';

export default function HistoryPage() {
    // 3. Panggil Logika dari Hook
    const {
        // Data & Loading
        transaksis,
        loading,
        
        // Filter State
        searchQuery,
        setSearchQuery,
        
        // Modal State
        selectedStruk,
        isDialogOpen,
        setIsDialogOpen,
        isProcessing,

        // Actions
        fetchHistory,
        handleViewDetail,
        handleUpdateStatus
    } = useRiwayatTransaksiPage();

    return (
        <div className="space-y-6 pb-20 p-6 md:p-8 max-w-7xl mx-auto">
            
            {/* Header Section */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Riwayat Transaksi</h1>
                    <p className="text-gray-500 text-sm">Daftar semua transaksi penjualan kasir.</p>
                </div>
                <Button variant="outline" size="sm" onClick={fetchHistory} disabled={loading}>
                    <RefreshCcw className={`w-4 h-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
                    Refresh Data
                </Button>
            </div>
            
            {/* Filter Bar */}
            <RiwayatFilter 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            {/* Tabel Data */}
            <RiwayatTable 
                loading={loading}
                transaksis={transaksis}
                onViewDetail={handleViewDetail}
            />

            {/* Modal Detail Struk & Konfirmasi */}
            <RiwayatStrukDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                data={selectedStruk}
                onUpdateStatus={handleUpdateStatus}
                isProcessing={isProcessing}
            />

            {/* CSS Khusus Print Struk */}
            <style jsx global>{`
                @media print {
                    /* Sembunyikan elemen layout utama saat print */
                    body * { visibility: hidden; }
                    nav, aside, header, footer { display: none !important; }
                    
                    /* Tampilkan hanya area struk di dalam dialog */
                    #struk-area, #struk-area * { visibility: visible; }
                    
                    /* Posisikan struk di pojok kiri atas */
                    #struk-area {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 100%;
                        margin: 0;
                        padding: 0;
                    }
                    
                    @page { margin: 0; size: auto; }
                }
            `}</style>
        </div>
    );
}