'use client';

import { 
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { CheckCircle2, XCircle, Printer, Loader2 } from 'lucide-react';

interface RiwayatStrukDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    data: any | null; 
    // ðŸ‘‡ Tambahkan definisi props baru ini agar error hilang
    onUpdateStatus?: (id: number, newStatus: string) => void; 
    isProcessing?: boolean;
}

export default function RiwayatStrukDialog({ 
    isOpen, 
    onOpenChange, 
    data,
    onUpdateStatus, // ðŸ‘‡ Ambil props ini
    isProcessing = false // ðŸ‘‡ Ambil props ini (default false)
}: RiwayatStrukDialogProps) {
    
    if (!data) return null;

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    const formatDate = (dateString: string) => 
        new Date(dateString).toLocaleDateString('id-ID', { 
            weekday: 'long', day: 'numeric', month: 'long', year: 'numeric', hour: '2-digit', minute: '2-digit' 
        });

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-md bg-white">
                <DialogHeader>
                    <DialogTitle className="text-center font-bold text-xl">Detail Transaksi</DialogTitle>
                    <div className="text-center text-gray-500 text-xs font-mono mt-1">
                        #{data.kode_transaksi}
                    </div>
                </DialogHeader>

                <div id="struk-area" className="py-2 space-y-4">
                    {/* Status Badge */}
                    <div className="flex justify-center">
                        {data.status === 'selesai' ? (
                            <Badge className="bg-green-600 hover:bg-green-700 text-white uppercase tracking-wider">Selesai</Badge>
                        ) : data.status === 'pending' ? (
                            <Badge variant="outline" className="text-blue-700 bg-blue-50 border-blue-200 uppercase tracking-wider">Menunggu Konfirmasi</Badge>
                        ) : (
                            <Badge variant="destructive" className="uppercase tracking-wider">Dibatalkan</Badge>
                        )}
                    </div>

                    <div className="grid grid-cols-2 text-sm gap-y-2 text-gray-600">
                        <span>Tanggal:</span>
                        <span className="text-right font-medium text-gray-900">{formatDate(data.created_at)}</span>
                        
                        <span>Pelanggan:</span>
                        <span className="text-right font-medium text-gray-900">{data.nama_pelanggan || 'Umum'}</span>
                        
                        <span>Kasir:</span>
                        <span className="text-right font-medium text-gray-900">{data.user?.name}</span>

                        {data.nomor_meja && (
                            <>
                                <span>No. Meja:</span>
                                <span className="text-right font-bold text-[#D93F21]">{data.nomor_meja}</span>
                            </>
                        )}
                    </div>

                    <Separator />

                    {/* Item List */}
                    <div className="space-y-2">
                        {data.details?.map((item: any, idx: number) => (
                            <div key={idx} className="flex justify-between text-sm">
                                <div>
                                    <span className="font-medium text-gray-800">{item.produk_resto?.nama_produk}</span>
                                    <div className="text-xs text-gray-500">
                                        {item.jumlah} x {formatRupiah(item.harga_saat_transaksi)}
                                        {item.catatan && <span className="italic text-orange-500 ml-1">({item.catatan})</span>}
                                    </div>
                                </div>
                                <span className="font-medium text-gray-900">
                                    {formatRupiah(item.subtotal)}
                                </span>
                            </div>
                        ))}
                    </div>

                    <Separator className="border-dashed" />

                    {/* Total */}
                    <div className="space-y-1">
                        <div className="flex justify-between font-bold text-lg text-gray-900">
                            <span>Total</span>
                            <span>{formatRupiah(data.total_harga)}</span>
                        </div>
                        <div className="flex justify-between text-sm text-gray-600">
                            <span>Bayar ({data.payment_method?.nama_metode})</span>
                            <span>{formatRupiah(data.total_bayar)}</span>
                        </div>
                        {Number(data.kembalian) > 0 && (
                            <div className="flex justify-between text-sm text-gray-600">
                                <span>Kembali</span>
                                <span>{formatRupiah(data.kembalian)}</span>
                            </div>
                        )}
                    </div>
                </div>

                <DialogFooter className="flex-col sm:flex-row gap-2">
                    {/* Tombol Aksi: HANYA muncul jika status 'pending' dan fungsi onUpdateStatus dikirim */}
                    {data.status === 'pending' && onUpdateStatus && (
                        <div className="grid grid-cols-2 gap-2 w-full">
                            <Button 
                                variant="destructive" 
                                className="w-full"
                                onClick={() => onUpdateStatus(data.id, 'dibatalkan')}
                                disabled={isProcessing}
                            >
                                {isProcessing ? <Loader2 className="w-4 h-4 animate-spin" /> : <XCircle className="w-4 h-4 mr-2" />}
                                Tolak
                            </Button>
                            <Button 
                                className="w-full bg-green-600 hover:bg-green-700 text-white"
                                onClick={() => onUpdateStatus(data.id, 'selesai')}
                                disabled={isProcessing}
                            >
                                {isProcessing ? <Loader2 className="w-4 h-4 animate-spin" /> : <CheckCircle2 className="w-4 h-4 mr-2" />}
                                Konfirmasi
                            </Button>
                        </div>
                    )}
                    
                    {/* Tombol Cetak / Tutup untuk status selain pending */}
                    {data.status !== 'pending' && (
                        <div className="flex gap-2 w-full justify-end">
                            <Button variant="outline" onClick={() => window.print()}>
                                <Printer className="w-4 h-4 mr-2" /> Cetak
                            </Button>
                            <Button variant="secondary" onClick={() => onOpenChange(false)}>
                                Tutup
                            </Button>
                        </div>
                    )}
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}